		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		margin-top: -10px;
		margin-bottom: 69px;

		@media(max-width: 375px) {
			justify-content: center;
			margin-right: 40px;
			margin-left: 40px;
		}
		@media(max-width: 600px) {
			margin-bottom: 50px;

		}
	}


	&__column {
		margin-top: 10px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		max-width: 125px;
		margin-right: 10px;
		&:last-of-type {
			margin-right: 0;
		}

  	}

	&__text {
		color: #505254;
 		font-size: 17px;
		font-weight: 500;
		line-height: 25px;
		text-align: center;
		margin-top: 20px;
	}
}
